import Vue from 'vue';

import Vuex from 'vuex';

Vue.use(Vuex);

import axios from 'axios';

const store  = new Vuex.Store({
    state:{
        token:"",
        movies:[]
    },

    mutations:{
        /** 保存token */
        saveToken(state,token){
            state.token = token;
        },
        logout(state){
            state.token = ""
        },
        /** 保存电影数据 */
        saveMovie(state,movies){
            state.movies = movies;
        }
    },


    actions:{
        /**  登录  */
        login(store,payload){
            return axios.post("http://localhost:8000/login",payload)
        },

        /** 获取电影列表数据 */
        getMovie(store){
    
            axios.get('/ajax/movieOnInfoList')
            .then(res=>{
                store.commit('saveMovie',res.data.movieList)
            })
            
        },
        /**  查询电影详情数据*/
        getDetail(store,id){
           
           return  axios.get('/ajax/detailmovie?movieId='+id)
           
        }
    }


})

export default store;