<template>
    <div>
        <h1>电影</h1>
        <u>
            <li v-for="m in movies" :key="m.id" @click="detail(m.id)">
                {{ m.nm }}
            </li>
        </u>
    </div>
</template>

<script>

import {mapActions,mapState} from 'vuex';
export default {
    mounted() {
        this.getMovie();
    },
    computed: {
        ...mapState(["movies"])
    },
    methods: {
        ...mapActions(["getMovie","getBooks"]),
        detail(id){
            this.$router.push({name:"detail",params:{id}})
        }
    },
}
</script>