<template>
    <div>
        <h1>{{detailMovie.nm}}</h1>
    </div>
</template>

<script>
import {mapActions} from 'vuex';
export default {
    data(){
        return {
            detailMovie:{}
        }
    },
    mounted() {
        let id = this.$route.params.id;
        console.log(id)
        this.getDetail(id).then((result) => {
            console.log(result)
            this.detailMovie = result.data.detailMovie;
        }).catch((err) => {
            
        });

  
    },
    methods: {
        ...mapActions(["getDetail","getBooks"])
    },  
}
</script>